         @click=\"See(item.id)\">驳回</a>\n          </td>\n          <td>\n            <a href=\"javascript:;\"\n               @click=\"adopt(item.id)\">确认</a>\n          </td>\n        </tr>\n      </tbody>\n    </table>\n    <el-dialog title=\"通过\"\n               :visible.sync=\"adoptDialogVisible\"\n               width=\"600\"\n               center>\n      <div class=\"adopt_dialog\">\n        <div>\n          <div ref=\"Mandatory\">两个必选项</div>\n          <div>\n            <label>收回物料</label>\n            <el-checkbox v-model=\"materiel\"\n                         class=\"checkbox\"></el-checkbox>\n            <label>退还押金</label>\n            <el-checkbox v-model=\"deposit\"\n                         class=\"checkbox\"></el-checkbox>\n          </div>\n          <div style=\"text-align: center\">\n            <el-button type=\"primary\"\n                       round\n                       @click=\"confirm\">确认</el-button>\n          </div>\n        </div>\n      </div>\n    </el-dialog>\n    <Reject-dialog :visible=\"centerDialogVisible\"\n                   @preservation=\"preservation\"\n                   @close=\"close\"></Reject-dialog>\n    <div class=\"page_center\"\n         v-show=\"pageCount&&pageCount>1\">\n      <paging :page-count=\"pageCount\"\n              :page=\"page\"\n              @index=\"paging\"></paging>\n      <!--分页的组件-->\n    </div>\n  </div>\n</template>\n<script>\nimport RejectDialog from \"@/components/Dialog/RejectDialog\"\nexport default {\n  data () {\n    return {\n      token: window.sessionStorage.getItem('bbytoken'),\n      page: 1,\n      pageCount: 1,\n      centerDialogVisible: false,\n      list: [],\n      centerDialogVisible: false,\n      adoptDialogVisible: false,\n      id: \"\",\n      Id: \"\",\n      radio: 1,\n      radio: 2,\n      materiel: true,\n      deposit: true\n    }\n  },\n  components: {\n    RejectDialog\n  },\n  created () {\n    this.init()\n  },\n  methods: {\n    //控制分页的组件\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    //关闭dialog的操作\n    close (Reason) {\n      this.centerDialogVisible = false;\n    },\n    //获取驳回理由\n    preservation (reason) {\n\n      if (reason) {\n        this.$axios.post(\"supply/rejectPost\", {\n          token: this.token,\n          id: this.id,\n          reason: reason\n        }).then(res => {\n\n          if (res.data.code == 1) {\n            var that = this\n            layer.msg(res.data.msg, { time: 1000 }, function () {\n              that.$router.push({ path: \"/MunicipalAgent/cancel/cancel/Reject\", query: { id: that.$route.query.id } })\n              that.close()\n            })\n          }\n        }).\n          catch(err => {\n            throw (err)\n          })\n      } else {\n        layer.msg(\"请输入驳回理由\")\n      }\n\n    },\n    init () {\n      this.$axios.post(\"supply/canApply\", {\n        token: this.token,\n        page: this.page\n      }).then(res => {\n\n        if (res.data.code == 1) {\n          this.list = res.data.data.list\n          this.pageCount = res.data.data.rows;\n        } else {\n          layer.msg(res.data.msg, { time: 500 })\n        }\n      }).\n        catch(err => {\n          throw (err)\n        })\n    },\n    details () {\n      var id = this.$route.query.id;\n      this.$router.push({\n        path: \"/MunicipalAgent/materiel/materiel/DetailsPage\",\n        query: {\n          id: id,\n          is: true\n        }\n      })\n    },\n    hoverColor (e) {\n      e.currentTarget.className = \"hoveColor\"\n    },\n    removeColor (e) {\n      e.currentTarget.className = \"primary\"\n    },\n    See (id) {\n      this.id = id;\n      this.centerDialogVisible = true;\n    },\n    //点击通过\n    adopt (id) {\n\n      this.adoptDialogVisible = true\n      this.Id = id;\n    },\n    //是否确认通过\n    confirm () {\n      if (!this.materiel || !this.deposit) {\n        this.$refs.Mandatory.className = \"Color-red\"\n        return;\n      } else {\n        this.$refs.Mandatory.className = \"Color-999\"\n      }\n      this.adoptData()\n    },\n    //通过操作\n    adoptData () {\n      this.$axios.post(\"supply/adoptPost\", {\n        token: this.token,\n        id: this.Id\n      }).then(res => {\n        if (res.data.code == 1) {\n          var that = this;\n          layer.msg(res.data.msg, { time: 1000 }, function () {\n            that.$router.push({ path: '/MunicipalAgent/cancel/cancel/adopt', query: { id: that.$route.query.id } })\n          })\n        }\n      }).\n        catch(err => {\n          throw (err)\n        })\n    }\n\n  }\n}\n</script>\n<style scoped>\n.hoveColor {\n  opacity: 0.5;\n}\n\n.primary {\n  color: #04bbfc;\n}\n\n.Reason {\n  display: flex;\n  margin-top: 30px;\n}\n\n.Color-red {\n  color: red;\n}\n\n.Color-999 {\n  color: #999999;\n}\n\ntextarea {\n  resize: none;\n  width: 80%;\n  height: 150px;\n  margin-left: 10px;\n}\n\n.adopt_dialog {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 69px 0 69px 0;\n}\n\n.adopt_dialog > div > div {\n  margin-bottom: 9px;\n}\n\n.adopt_dialog > div > div > button {\n  margin-top: 60px;\n  padding: 10px 30px;\n}\n\n.checkbox {\n  margin: 0 8px;\n  border-radius: 50% !important;\n}\n</style>\n<style>\n.adopt_dialog .el-checkbox__input.is-checked .el-checkbox__inner,\n.el-checkbox__input.is-indeterminate .el-checkbox__inner {\n  border-radius: 50%;\n}\n</style>\n"],"sourceRoot":""}]);

